<script>
// @ is an alias to /src  浅蓝rgb(49,63,82)   深蓝rgb(47,55,66)
import draggable from "vuedraggable";
export default {
  components: {
    draggable,
  },
  data() {
    return {
      rate_country_list: [], //显示的国家货号列表
      country_list: [], //国家代码
      drawer: false,
      lazada_tag: true,
      shopee_tag: true,
      amazon_tag: true,
      aliexpress_tag: true,
      switch_width: 60,
      info_tag: true,
    };
  },
  created() {
    //console.log(process.env.NODE_ENV)
    this.resh_go();
    let self = this;
    chrome.storage.local.get(
      ["lazada_tag", "shopee_tag", "amazon_tag", "aliexpress_tag", "info_tag"],
      function (s) {
        const {
          lazada_tag,
          shopee_tag,
          amazon_tag,
          aliexpress_tag,
          info_tag,
        } = s;
        self.lazada_tag = lazada_tag;
        self.shopee_tag = shopee_tag;
        self.amazon_tag = amazon_tag;
        self.aliexpress_tag = aliexpress_tag;
        self.info_tag = info_tag; //是否显示拖动提示
      }
    );
    console.log(this);
  },
  methods: {
    showOption() {
      /*       if(this.country_list.length > 4){  //360专用判断
        this.$message({type:'error',message:'请把转换列表中的货币数量删除到4个,再打开设置'})
      }else{
        this.drawer = true
      } */
      this.drawer = true;
    },
    lazada_chang(val) {
      chrome.storage.local.set({ lazada_tag: val }, function (s) {});
    },
    shopee_chang(val) {
      chrome.storage.local.set({ shopee_tag: val }, function (s) {});
    },
    amazon_chang(val) {
      chrome.storage.local.set({ amazon_tag: val }, function (s) {});
    },
    aliexpress_chang(val) {
      chrome.storage.local.set({ aliexpress_tag: val }, function (s) {});
    },
    info_chang(val){
       chrome.storage.local.set({ info_tag: val }, function (s) {});
    },
    del_item(item) {
      let b = item.tcur;
      let c = []; //新数组
      this.country_list.forEach((el) => {
        //console.log('当前国家代码:',el)
        if (el === b) {
        } else {
          c.push(el);
        }
      });
      this.country_list = c;

      let self = this;
      chrome.storage.local.set({ my_rate_zz_countries: c }, function (s) {
        let d = "";
        for (let i = 0; i < self.rate_country_list.length; i++) {
          if (self.rate_country_list[i].tcur === item.tcur) {
            d = i;
            break;
          }
        }
        self.rate_country_list.splice(d, 1);
      });
    },
    resh_go() {
      let self = this;
      if (process.env.NODE_ENV === "development") {
        return;
      }
      chrome.storage.local.get(
        ["my_rate_zz", "my_rate_zz_countries"],
        function (result) {
          console.log("从storage中取出来的汇率数据为:");
          console.log(result);
          let { my_rate_zz, my_rate_zz_countries } = result;
          if (!my_rate_zz_countries) {
            my_rate_zz_countries = ["CNY", "USD", "TWD"];
            chrome.storage.local.set(
              { my_rate_zz_countries: my_rate_zz_countries },
              function (s) {
                console.log("初始化国家列表");
              }
            );
          }
          self.country_list = my_rate_zz_countries;
          let c = [];
          self.country_list.forEach((el) => {
            let rate_country = {};
            rate_country.country_img = my_rate_zz[el].flagURL;
            rate_country.currency_symbol = my_rate_zz[el].symbol;
            rate_country.num = "";
            rate_country.currency_name = my_rate_zz[el].name;
            rate_country.currency_status = "";
            rate_country.tcur = my_rate_zz[el].tcur;
            rate_country.rate = my_rate_zz[el].rate;
            //console.log(rate_country)
            c.push(rate_country);
          });
          self.rate_country_list = c;
        }
      );
    },
    popgo() {
      var myWidth = 405;
      var myHeight = 620;
      var left = (screen.width - myWidth) / 1.05;
      var top = 60;
      window.open(
        location.href,
        "popUpWindow",
        "scrollbars=no,resizable=no,titlebar=no,toolbar=no,menubar=no,location=no,directories=no,status=yes,width=" +
          myWidth +
          ",height=" +
          myHeight +
          ",top=" +
          top +
          ", left=" +
          left
      );
      // window.close();
    },
    startgo() {
      window.open(
        "https://chrome.google.com/webstore/detail/%E6%B1%87%E7%8E%87%E8%BD%AC%E6%8D%A2/bcpgdpedphodjcjlminjbdeejccjbimp/reviews"
      );
    },
    oninput(val) {
      //console.log(val)
      let a = [];
      let rate_now = Number(val.rate);
      this.rate_country_list.forEach((el) => {
        if (val.tcur === el.tcur) {
          el.currency_status = `1 ${el.tcur} = 1 ${el.tcur}`;
          a.push(el);
        } else {
          el.num = (
            (Number(el.rate) / Number(val.rate)) *
            Number(val.num)
          ).toFixed(4);
          el.currency_status = `1 ${val.tcur} = ${(
            Number(el.rate) / Number(val.rate)
          ).toFixed(4)} ${el.tcur}`;
        }
      });
    },
    goto_add() {
      this.$router.push("/add_list");
    },
    sortChang(v) {
      console.log("排序改变", this.rate_country_list);
      let a = this.rate_country_list.map((e) => {
        return e.tcur;
      });
      chrome.storage.local.set({ my_rate_zz_countries: a }, function (s) {});
    },
  },
};
</script>
